<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import PCAskItem from '../../page/home/children/PCAskItem'
import PCExpertItem from '../../page/home/children/PCExpertItem'
import $ from 'webpack-zepto'
import httpTool from '../../utils/httpTool'
import PCNewAskList from './pc/PCNewAskList'
import PCCommonNav from '../../components/common/PCCommonNav'
import CFWPageTest from '../../components/common/CFWPageTest'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userToken:'',
      userInfo:null,
      isLogin:false,
      searchStr:'',
      list:[],
      complete: false, // 
      loading: false, // 
      pageSize:10, // 10
      isGet:true,
      ask_category:[
          {id:1,name:''},
          {id:3,name:''},
          {id:2,name:''},
          {id:4,name:''},
      ],
      sid:0, // 
      type:1,
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    this.type = this.$route.query.type

    // 
    if (this.$route.query.sid) {
      this.sid = this.$route.query.sid
    }

    if (this.userID) {
      this.isLogin = true
    }

    // 
    this.isMobile()
  },
  filters:{
  },
  mounted(){
    let keyStr = this.$route.query.keyStr
    if (keyStr) {
      this.searchText = decodeURI(decodeURI(this.$route.query.keyStr))
    }

    if (this.searchText != '') {
      this.getData()
    }

  },
  components:{
    PCHeader,
    PCFooter,
    PCExpertItem,
    PCAskItem,
    PCNewAskList,
    PCCommonNav,
    CFWPageTest
  },
  computed:{
  },
  methods: {
    gotoPage(){

    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    // 
    getData(){
      Indicator.open();
      let pStr = '?s='+this.searchText

      let methods = this.type == 1 ? 'list' : 'baike'

      let getUrl = this.CFWConfig.APIHost + '/v3/ask/'+methods+pStr+'&sid='+this.sid
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.list = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('');
        console.log(response);
        Indicator.close();
      });
    },
    seeing () {
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },

}
</script>

